import React, { useEffect, useState } from "react";

import "./FeaturedBlogBanner.css";

import axios from "axios";

import { BiRightArrowAlt } from "react-icons/bi";

import { FaRegCommentDots } from "react-icons/fa";

import book1 from "./Book1.png";

import { Link } from "react-router-dom";

import { MdSignalWifiConnectedNoInternet0 } from "react-icons/md";

export default function FeaturedBlogBanner() {
  const [posts, setPosts] = useState([]);

  const [errorMsg, setErrorMessage] = useState(null);

  useEffect(() => {
    let url =
      "https://dev-interac8digital.pantheonsite.io/wp-json/wp/v2/posts?_embed&categories=4";

    axios
      .get(url)
      .then((response) => {
        setPosts(response.data);
        console.log(response.data.length);
        console.log(response.data[0]);
        console.log(response.data[1]);
        console.log(response.data[2]);
      })
      .catch((error) => {
        setErrorMessage(
          "Cannot load blogs. You are not connected to the internet"
        );
      });
  }, []);

  const convertDate = (date) => {
    const isoDate = new Date(date);

    // Format the date in the desired format
    const options = { day: "numeric", month: "numeric", year: "numeric" };
    const formattedDate = isoDate.toLocaleDateString("en-US", options);

    return formattedDate;
  };

  return (
    <>
      {errorMsg == null ? (
        <div className="featured-blog-banner-main-container">
          <div className="featured-posts">
            <h1>Featured Posts</h1>
            <div className="featured-posts-line"></div>
          </div>

          <div className="featured-blog-banner-inner-container">
            {posts[0] && posts[0]._embedded["wp:featuredmedia"][0] && (
              <Link
                to={`/blogNewPage?id=${posts[0].id}`}
                style={{
                  backgroundImage: `url(${
                    posts[0]
                      ? posts[0]._embedded["wp:featuredmedia"][0].media_details
                          .sizes.full.source_url
                      : book1
                  })`,
                  width: "100%",
                }}
                className="featured-blog-banner-inner-container-first"
              >
                <div className="read-more-div">
                  {/* <h2>{posts[0] ? posts[0].title.rendered : "No  title"}</h2> */}
                  <h2>Read more...</h2>
                </div>

                <h4>{posts[0] && convertDate(posts[0].date)}</h4>

                <FaRegCommentDots
                  className="comments-blogs"
                  color="#fff"
                  size={30}
                />

                <p>{posts[0]._links.replies[0].count}</p>
              </Link>
            )}

            {posts[1] && posts[1]._embedded["wp:featuredmedia"][0] && (
              <div className="featured-blog-banner-inner-container-second">
                <Link
                  to={`/blogNewPage?id=${posts[1].id}`}
                  style={{
                    backgroundImage: `url(${
                      posts[1]
                        ? posts[1]._embedded["wp:featuredmedia"][0]
                            .media_details.sizes.medium_large.source_url
                        : book1
                    })`,
                  }}
                  className="featured-blog-banner-inner-container-second-first-blog"
                >
                  <div className="read-more-div">
                    {/* <h2>{posts[1] ? posts[1].title.rendered : "No  title"}</h2> */}
                    <h2>Read more...</h2>
                    {/* <BiRightArrowAlt
                  color="#fff"
                  size={35}
                  className="read-more-icon"
                /> */}
                  </div>

                  <h4>{posts[1] && convertDate(posts[1].date)}</h4>

                  <FaRegCommentDots
                    className="comments-blogs"
                    color="#fff"
                    size={30}
                  />

                  <p>{posts[1]._links.replies[0].count}</p>
                </Link>

                {posts[2] && posts[2]._embedded["wp:featuredmedia"][0] && (
                  <div className="featured-blog-banner-inner-container-second-second-blog">
                    <div className="featured-blog-banner-inner-container-second-second-blog-one">
                      <div className="read-more-div">
                        <h2>Read more</h2>
                        {/* <BiRightArrowAlt
                      color="#fff"
                      size={35}
                      className="read-more-icon"
                    /> */}
                      </div>

                      <h4>{posts[2] && convertDate(posts[2].date)}</h4>

                      <FaRegCommentDots
                        className="comments-blogs"
                        color="#fff"
                        size={20}
                      />

                      <p>{posts[2]._links.replies[0].count}</p>
                    </div>

                    {posts[3] && posts[3]._embedded["wp:featuredmedia"][0] && (
                      <div className="featured-blog-banner-inner-container-second-second-blog-two">
                        <div className="read-more-div">
                          <h2>Read more</h2>
                          <BiRightArrowAlt
                            color="#fff"
                            size={35}
                            className="read-more-icon"
                          />
                        </div>

                        <h4>{posts[3] && convertDate(posts[3].date)}</h4>

                        <FaRegCommentDots
                          className="comments-blogs"
                          color="#fff"
                          size={20}
                        />

                        <p>{posts[3]._links.replies[0].count}</p>
                      </div>
                    )}
                  </div>
                )}
              </div>
            )}
          </div>
        </div>
      ) : (
        <>
          <div
            style={{
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              justifyContent: "center",
              padding: "30px"
            }}
          >
            <MdSignalWifiConnectedNoInternet0 color={"#612d91"} size={200} />
            <h2>Cannot load blogs. You are not connected to the internet</h2>
          </div>
        </>
      )}
    </>
  );
}
