import React, { useState, useEffect, CSSProperties } from "react";

import ClipLoader from "react-spinners/ClipLoader";

import axios from "axios";

import "./BlogMain.css";

import { AiFillPlusCircle } from "react-icons/ai";

import { AiFillMinusCircle } from "react-icons/ai";

import { MdOpenInNew } from "react-icons/md";

import { MdSignalWifiConnectedNoInternet0 } from "react-icons/md";

import backUpImage from "./test.jpg";

import { Link } from "react-router-dom";

export default function BlogMain() {
  // This piece of code fetches the post from wordpress and saves it in the variable post.
  // Axios is a third party package that was used to fetch the data from wordpress.
  // Data from wordpress is coming in form of an array which contains object. Each object is a single wordpress post.
  //You can run this "https://dev-interac8digital.pantheonsite.io/wp-json/wp/v2/posts" in thunder client to see the response we are getting from wordpress

  const [posts, setPosts] = useState([]);
  const [postsLength, setPostsLength] = useState(0);
  const [errorMsg, setErrorMessage] = useState(null);

  useEffect(() => {
    let url =
      "https://dev-interac8digital.pantheonsite.io/wp-json/wp/v2/posts?_embed";

    axios
      .get(url)
      .then((response) => {
        setPosts(response.data);
        setPostsLength(posts.length);
      })
      .catch((error) => {
        setErrorMessage(
          "Cannot load blogs. You are not connected to the internet"
        );
      });
  }, []);

  // Wordpress API sends us the date in this format: "2023-07-24T11:50:07".
  //We want date to be in this format: "24/7/2023"
  //So the "formatDate" function will convert "2023-07-24T11:50:07" to "24/7/2023"
  function formatDate(apiDate) {
    const dateObject = new Date(apiDate);
    const day = dateObject.getDate();
    const month = dateObject.getMonth() + 1; // Months are zero-indexed, so we add 1 to get the correct month number
    const year = dateObject.getFullYear();

    return `${day}/${month}/${year}`;
  }

  // Create an array of boolean values to track the expansion state of each card
  const [expansions, setExpansions] = useState(Array(5).fill(false));

  // Function to toggle the expansion state of a specific card. This piece of code makes sure that only the card which is clicked will open or close. Rest of the cards will not change.
  //Code was copied from chatGPT, so good luck understanding it because I don't understand it either.
  const toggleExpansion = (index) => {
    setExpansions((prevExpansions) => {
      const updatedExpansions = [...prevExpansions];
      updatedExpansions[index] = !updatedExpansions[index];
      return updatedExpansions;
    });
  };

  let [loading, setLoading] = useState(true);
  let [color, setColor] = useState("#ffffff");

  const override = {
    display: "block",
    margin: "0 auto",
    borderColor: "#612d91",
  };

  return (
    <div>
      {(errorMsg == null) ? <><div className="blog-card-featured-posts">
        <h1>All Posts</h1>
        <div className="featured-posts-line"></div>
      </div>
      <div className="blog-card-main-container">
        {/* posts.map will take the array of Posts and it will go through each item in the array. So, for example, if you get 4 posts from wordpress, the loop will automatically run 4 times and will print 4 cards along with their information. */}
        {posts.length > 0 ? (
          posts.map((element, index) => {
            let date = formatDate(element.date);

            let featuredImageUrl;

            try {
              // Extract the featured image URL
              featuredImageUrl =
                element._embedded["wp:featuredmedia"][0].media_details.sizes
                  .medium_large.source_url;
            } catch (error) {
              featuredImageUrl = backUpImage;
            }

            return (
              <div key={index} className="blog-card-container">
                <div
                  className="blog-card-container-image"
                  style={{ backgroundImage: `url(${featuredImageUrl})` }}
                ></div>

                <div className="blog-card-container-date">
                  Posted on: {date}
                </div>

                <div className="blog-card-container-title">
                  <h2>{element.title.rendered}</h2>
                </div>

                <div
                  className={`blog-card-container-content ${
                    expansions[index] ? "expanded" : ""
                  }`}
                >
                  <span
                    dangerouslySetInnerHTML={{
                      __html: `${
                        !expansions[index]
                          ? element.excerpt.rendered
                          : element.content.rendered
                      }`,
                    }}
                  ></span>

                  {!expansions[index] ? (
                    <AiFillPlusCircle
                      color={"#612d91"}
                      size={30}
                      className="blog-plus-sign"
                      onClick={() => {
                        toggleExpansion(index); // Call the toggleExpansion function with the index of the clicked card
                      }}
                    />
                  ) : (
                    <AiFillMinusCircle
                      color={"#612d91"}
                      size={30}
                      className="blog-plus-sign"
                      onClick={() => {
                        toggleExpansion(index); // Call the toggleExpansion function with the index of the clicked card
                      }}
                    />
                  )}
                </div>

                <div className="blog-card-container-button">
                  <button>
                    <Link to={`/blogNewPage?id=${element.id}`} target="_blank">
                      Open in new tab <MdOpenInNew color={"#fff"} size={20} />
                    </Link>
                  </button>
                </div>
              </div>
            );
          })
        ) : (
          <h1>
            {errorMsg == null ? (
              <ClipLoader
                color={color}
                loading={loading}
                cssOverride={override}
                size={150}
                aria-label="Loading Spinner"
                data-testid="loader"
              />
            ) : (
              <MdSignalWifiConnectedNoInternet0 color={"#612d91"} size={100} />
            )}
          </h1>
        )}
      </div></> : (<h1></h1>)}
    </div>
  );
}
